@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,100&display=swap);
*{font-family:'Roboto', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:16px;color:white}* button{outline:none;background:none;border:none}body{margin:0;overflow-x:hidden}

.selected,.nav-item-btn{border:none;outline:none;background:none;padding:0;display:flex;align-items:center}.selected .item-text,.nav-item-btn .item-text{box-sizing:border-box;margin-left:0.5rem;transition:all ease 0.3s;border-bottom:0.1rem solid transparent}.nav-item-btn svg{margin:0}.nav-item-btn svg *{fill:white}.selected .item-text{color:#ef233c;border-bottom:0.1rem solid #ef233c}.selected svg{margin:0}.selected svg *{fill:#ef233c}.nav-item-btn:hover{cursor:pointer}.nav-item-btn:hover .item-text{border-bottom:0.1rem solid white}

.menu-bar-container{display:none}@media (min-width: 768px){.menu-bar-container{display:flex;align-items:center}.menu-bar-container *{margin-left:1.25rem}}

.open-btn,.close-btn{border:none;outline:none;background:none;padding:0;display:flex;align-items:center}.close-btn svg{fill:none;stroke:#ef233c;stroke-linecap:round;stroke-width:2px}.open-btn svg{fill:none;stroke:#fff;stroke-linecap:round;stroke-width:2px}@media (min-width: 768px){.open-btn,.close-btn{display:none;visibility:hidden}}

.header-right-container{display:flex;align-items:center}

.header-container{background-color:#222;display:flex;justify-content:space-between;align-items:center;padding:1rem 1.8rem;box-sizing:border-box;width:100%}.header-container .left{display:flex;align-items:center}.header-container .left .header-logo,.header-container .left .presentation-text{margin-right:1.25rem}.header-container .left .header-logo{background-color:#ef233c;border-radius:100%;line-height:0.5rem;padding:0.8rem}.header-container .left .header-logo svg path{fill:white}.header-container .left .presentation-text .name{text-transform:uppercase;text-decoration:underline;font-style:italic}.header-container .left .logout{display:none}.menu-open{background:#ef233c;border-bottom:solid 0.1rem white}.menu-open .left .header-logo{background:white}.menu-open .left .header-logo svg path{fill:#ef233c}@media (min-width: 768px){.header-container .left .logout{display:flex;align-items:center;border-left:1px solid white;padding-left:1.25rem}.header-container .left .logout img{margin-right:0.5rem}.header-container .right .button-mobile{display:none}}

.lateral-menu{color:black;background:#ef233c;height:100vh;width:100%;overflow:hidden;display:flex;flex-direction:column;align-items:center}.lateral-menu .lateral-menu-items{padding-top:1rem}.lateral-menu .lateral-menu-items .lateral-menu-item{padding:1rem;background-color:#ef233c}

.fill-mine-shaft{fill:#222}.fill-white{fill:white}.fill-primary{fill:#ef233c}

.footer-container-primary{background:#222;display:flex;justify-content:center;align-items:center;padding:2rem 0}.footer-container-white{background:#ef233c;position:fixed;bottom:0;left:0;width:100%;display:flex;justify-content:center;align-items:center;padding:2rem 0}.footer-container-primary,.footer-container-white{display:flex;justify-content:center;align-items:center}@media (min-width: 768px){.footer-container-primary{padding:4.25rem 0}}

.introduction-container{display:flex;flex-direction:column;align-items:center;background:#0d455d 0% 0% no-repeat padding-box;font-family:'Roboto'}.introduction-container .introduction-title{border-left:0.15rem solid white;margin:1.85rem;padding-left:1.75rem;font:italic normal 300 28px/30px Roboto;letter-spacing:0;font-size:1.75rem;line-height:1.85rem}.introduction-container .introduction-description{text-align:left;font:normal normal 300 16px/24px Roboto;letter-spacing:0;margin:0 1.85rem 2rem 1.85rem}@media (min-width: 768px){.introduction-container{padding:5rem 2.5rem;justify-content:center;align-items:flex-start;box-sizing:border-box}.introduction-container .introduction-description,.introduction-container .introduction-title{margin:0}.introduction-container .introduction-title{margin-bottom:1.5rem}}@media (min-width: 1280px){.introduction-container{height:100%;width:50%;padding:0 6.25rem}.introduction-container .introduction-title{display:block;margin:0 0 1.8rem 0;padding:0 0 0 1rem}.introduction-container .introduction-description{margin:0}}

.right-white path{fill:white}.left-white{transform:rotate(180deg)}.left-white path{fill:white}.down-white{transform:rotate(90deg)}.down-white path{fill:white}.up-white{transform:rotate(-90deg)}.up-white path{fill:white}.down-copper{transform:rotate(90deg);width:0.65rem;height:auto;margin:0}.down-copper path{fill:#9f5151}.up-finch{transform:rotate(-90deg);width:0.65rem;height:auto;margin:0}.up-finch path{fill:#5e6a4d}

.explore-button{background:#ef233c;color:white;padding:1rem;display:flex;justify-content:center;align-items:center;box-sizing:border-box;width:100%}.explore-button svg{margin-left:0.8rem;transition:all ease 0.2s}.explore-button svg path{fill:white}.explore-button:hover{cursor:pointer;background:#e8112b}.explore-button:hover svg{transform:translate(4px)}.explore-button.curved{border-radius:0px 0px 0px 12px}

.investment-option-container{background:#f0f0f3;padding:2.2rem;box-sizing:border-box;display:flex;flex-direction:column;width:100%}.investment-option-container .investment-description{display:flex;flex-direction:column}.investment-option-container .investment-description .option-title{font-style:italic;font-weight:bold;letter-spacing:0px;color:#ef233c;border-left:0.15rem solid #ef233c;font-size:1.75rem;padding-left:1.25rem;box-sizing:border-box}.investment-option-container .investment-description .option-description{color:#222;line-height:1.5rem;margin-top:1.75rem}.investment-option-container>button:last-child{margin-top:2rem}@media (min-width: 1280px){.investment-option-container{max-width:45%;display:flex;justify-content:space-between;margin:0.5rem}}@media (min-width: 1800px){.investment-option-container{width:35%;margin:1.25rem}}

.investment-options-container{margin:2rem}.investment-options-container>:last-child{border-radius:0px 0px 0px 24px;margin-top:2rem}@media (min-width: 768px){.investment-options-container{display:flex;flex-direction:column;justify-content:center;align-items:center;margin-bottom:0}}@media (min-width: 1280px){.investment-options-container{flex-direction:row;align-items:unset;width:50%;margin:0}.investment-options-container>:last-child{border-radius:unset;margin-top:0}.investment-options-container>:first-child{border-radius:0px 0px 0px 24px;margin-top:0}}

.main-content-container{display:flex;flex-direction:column;align-items:center}@media (min-width: 768px){.main-content-container{margin-bottom:2rem}}@media (min-width: 1280px){.main-content-container{margin-bottom:0;height:70vh;display:flex;flex-direction:row;align-items:center}}

.product-details-finch,.product-details-copper{display:flex;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:0 0.5rem;box-sizing:border-box}.product-details-finch .value,.product-details-finch .variation,.product-details-copper .value,.product-details-copper .variation{font-size:0.9rem;font-weight:bold}.product-details-finch .value,.product-details-copper .value{margin-right:0.5rem}.product-details-finch .variation,.product-details-copper .variation{display:flex;align-items:center;justify-content:center}.product-details-finch .variation .variation-number,.product-details-copper .variation .variation-number{margin-left:0.1rem}.product-details-finch{background:#e5ffc7}.product-details-finch .value,.product-details-finch .variation-number{color:#5e6a4d}.product-details-copper{background:#ffc7c7}.product-details-copper .value,.product-details-copper .variation-number{color:#9f5151}

.product-container .product-information{display:flex;background:white;padding:1rem 1.2rem;box-sizing:border-box}.product-container .product-information .product-name{color:#ef233c;font-size:1rem;margin-right:1rem;font-weight:bold;font-style:italic}.product-container .product-information .product-name .liqid{color:inherit;text-transform:uppercase}@media (min-width: 768px){.product-container{width:100%;display:flex}.product-container .product-information{width:100%}.product-container button{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}}

.sorting-button{display:flex;align-items:center;justify-content:center;border-radius:2px;background-color:#b9b9b9;padding:0.2rem 1rem;font:normal normal normal 1rem Roboto;letter-spacing:0px;cursor:pointer}.sorting-button span{margin:0 0.5rem 0 0}.sorting-button-active{background-color:#6c6c6c}

.user-products-container{display:flex;flex-direction:column;background:#f0f0f3;padding:2rem}.user-products-container .user-products-title{font-style:italic;font-weight:bold;letter-spacing:0px;color:#ef233c;border-left:0.15rem solid #ef233c;padding-left:1.25rem;box-sizing:border-box;margin-bottom:2rem;font-size:2.2rem}.user-products-container .sort-section{display:flex;margin-bottom:1.9rem}.user-products-container .sort-section .sort-title{color:#ef233c;font-weight:bold;font-size:1.2rem;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.user-products-container .sort-section .sorting-buttons{margin-left:1rem;display:flex;flex-direction:column}.user-products-container .sort-section .sorting-buttons>button:first-child{margin-bottom:0.8rem}.user-products-container .user-products{display:flex;flex-direction:column;border-radius:0 0 0 12px;overflow:hidden}.user-products-container .user-products>:not(:last-child){margin-bottom:0.8rem}@media (min-width: 768px){.user-products-container .user-products{border-radius:0}}@media (min-width: 1280px){.user-products-container{display:flex;flex-direction:column;justify-content:center;align-items:center;padding:4rem 0}.user-products-container .user-products-title{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.user-products-container .sort-section,.user-products-container .user-products{width:70%}.user-products-container .sort-section .sorting-buttons{flex-direction:row}.user-products-container .sort-section .sorting-buttons>button:first-child{margin-bottom:0rem;margin-right:0.8rem}}@media (min-width: 1800px){.user-products-container{padding:6.25rem 0}}

.app{width:100%;overflow:hidden}.app .header-wrapper .lateral{width:100%;position:fixed;top:0;left:0;z-index:10;height:100vh}.app .header-wrapper .hide{transform:translateX(800px);transition:all ease-out 0.5s}.app .header-wrapper .show{transform:translateX(0);transition:all ease-in 0.6s}@media (min-width: 768px){.app .header-wrapper .lateral{display:none}}

